@import url("https://fonts.googleapis.com/css2?family=Inter:wght@700&family=Roboto:wght@300;400;500;700;900&display=swap");

html {
  scroll-behavior: smooth;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.degrade {
  background: linear-gradient(90.13deg, #d91dac -7.34%, #4100cc 105.21%);
  border-radius: 20px;
  transition:  0.9s ease;
}


.degradetext {
  background: #ff0096;
  background: linear-gradient(to right, #ff0096 0%, #4100cc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.icon {
  color: #ff0096;
}

.card {
  background: white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}

.card2 {
  background: #f7f7f7;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}

.progress-bar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 10px;
  background: "red";
  transform-origin: 0%;
}

.btn{}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
